// Sprites Map
$sp_ico: (
	name: 'sp_ico',
	width: 80px,
	height: 54px,
	image: '../img/sp_ico.png',
	sprites: (
	ico-active-left: (
			offset-x: 0px,
			offset-y: 0px,
			width: 30px,
			height: 25px,
		),
		ico-active-right: (
			offset-x: -34px,
			offset-y: 0px,
			width: 30px,
			height: 25px,
		),
		ico-default-left: (
			offset-x: 0px,
			offset-y: -29px,
			width: 30px,
			height: 25px,
		),
		ico-default-right: (
			offset-x: -34px,
			offset-y: -29px,
			width: 30px,
			height: 25px,
		),
		ico-triangle-top: (
			offset-x: -68px,
			offset-y: 0px,
			width: 12px,
			height: 12px,
		),
	)
);
