// layout

.wrap-coverage-picker{
  position: relative;
  border: 2px solid #e7ecf0;
  padding: 30px 75px 0 75px;
  height: 204px;
  .title-txt{
    padding-top: 46px;
    position: absolute;
    left: 26px;
    top: 30px;
    font-size: 13px;
    .item{
      display: block;
      font-weight: 400;
    }
  }
  .slider-coverage{
    position: relative;
    overflow: hidden;
    font-size: 0;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      left: 0;
      top: 10px;
      height: 6px;
      background-color: #8b97a5;
      border-radius: 3px;
    }
  }
  .list-coverage{
    display: table;
    width: 100%;
    table-layout: fixed;
  }
  .cell-coverage{
    display: table-cell;
    text-align: center;
    width: 124px;
    vertical-align: top;
    padding-top: 33px;
  }
  .inner-btn{
    position: relative;
    display: inline-block;
    width: 120px;
    height: 103px;
    line-height: 20px;
    color: #353942;
    text-decoration: none;
    font-weight: 400;
    padding-top: 13px;
    vertical-align: top;
    font-size: 14px;
    .point-orange{
      color: #fd6e0e;
    }

    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: -31px;
      right: 0;
      width: 18px;
      height: 18px;
      border: 1px solid #838c93;
      margin: auto;
      border-radius: 50%;
      background-color: #fff;
      box-sizing: border-box;
    }

    
  }
  .inner-btn[aria-selected="true"]{
    font-weight: 500;
    border: 1px solid #ff7800;
    &:before{
      border: 3px solid #ff7800;
    }
    &:after{
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      bottom: 100%;
      background-color: #fff;
      @include get-sprite('ico-triangle-top', $sp-ico);
    }
  }
  .picker-btn{
    position: absolute;
    width: 60px;
    height: 50px;
    top: 30px;
    cursor: not-allowed;

    &.type-prev{
      position: absolute;
      left: 24px;
      @include get-sprite('ico-default-left', $sp-ico);
    }
    &.type-next{
      position: absolute;
      right: 24px;
      @include get-sprite('ico-default-right', $sp-ico);
    }
    &.type-next[aria-disabled="false"]{
      @include get-sprite('ico-active-right', $sp-ico);
    }
    &.type-prev[aria-disabled="false"]{
      @include get-sprite('ico-active-left', $sp-ico);
    }
    &[aria-disabled="false"]{
      cursor: pointer;
    }
  }
}

